<template>
<div class="test">
  <Header title="博乐环境" english_title="Bole environment"/>

  <div class="container">
    <swiper class="swiper" :options="swiperOptions">
      <swiper-slide class="slide-1"></swiper-slide>
      <swiper-slide class="slide-2"></swiper-slide>
      <swiper-slide class="slide-3"></swiper-slide>
      <swiper-slide class="slide-4"></swiper-slide>
      <swiper-slide class="slide-5"></swiper-slide>
      <swiper-slide class="slide-6"></swiper-slide>
      <swiper-slide class="slide-7"></swiper-slide>
      <swiper-slide class="slide-8"></swiper-slide>
      <swiper-slide class="slide-9"></swiper-slide>
      <div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>
      <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
      <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
    </swiper>
  </div>
</div>
</template>

<script>
import Header from '../../components/Header'
export default {
  name: "Environment",
  components:{
    Header
  },
  data(){
    return{
      swiperOptions: {
        slidesPerView: 1,
        spaceBetween: 10,
        slidesPerGroup: 1,
        loop: true,
        loopFillGroupWithBlank: false,
        autoplay:{
          delay: 3000,
          disableOnInteraction: true,
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  }
}
</script>


<style scoped>
@media screen and (min-width: 800px){
  .swiper {
    height: calc(80vh);
  }
}
@media screen and (max-width: 800px){
  .swiper {
    height: calc(40vh);
  }
}

swiper-slide {
  background-position: center;
  background-size: cover;
}
/* 图片暂时放在 sm.ms 上，后续可以放在 CDN 上加速，资源备份在 /static/env_bole 中 */
.slide-1 {
  background-image:url('https://h5.store.bolegames.com/bole_campus/static/env_bole/env_01.jpg');
  background-position: center;
  background-size: cover;
 }
.slide-2 {
  background-image:url('https://h5.store.bolegames.com/bole_campus/static/env_bole/env_02.jpg');
