  background-size: cover;
 }
.slide-4{
  background-image: url("https://h5.store.bolegames.com/bole_campus/static/env_bole/env_04.jpg");
  background-position: center;
  background-size: cover;
}

.slide-5 {
  background-image:url('https://h5.store.bolegames.com/bole_campus/static/env_bole/env_05.jpg');
  background-position: center;
  background-size: cover;
}
.slide-6{
  background-image: url("https://h5.store.bolegames.com/bole_campus/static/env_bole/env_06.jpg");
  background-position: center;
  background-size: cover;
}

.slide-7 {
  background-image:url('https://h5.store.bolegames.com/bole_campus/static/env_bole/env_07.jpg');
  background-position: center;
  background-size: cover;
}
.slide-8{
  background-image: url("https://h5.store.bolegames.com/bole_campus/static/env_bole/env_08.jpg");
  background-position: center;
  background-size: cover;
}

.slide-9 {
  background-image:url('https://h5.store.bolegames.com/bole_campus/static/env_bole/env_09.jpg');
  background-position: center;
  background-size: cover;
}
</style>



