<template>
<div class="test">
  <Header title="学长学姐说" english_title="Voice of employees"/>
  <div class="container">
    <div class="layout">
      <video class="video" preload="auto" width="100%" height="100%" controls>
        <source src="https://h5.store.bolegames.com/bole_campus/assets/BrotherAndSisterTalk/talk.mp4" type="video/mp4"/>
      </video>
    </div>

  </div>
</div>
</template>

<script>
import Header from '../../components/Header'
export default {
  name: "BrotherAndSisterTalk",
  components:{
    Header
  }
}
</script>

<style scoped>
@media  screen and (min-width: 800px){
  .container{
    /*border: red solid 1px;*/
  }

  .layout{
    width: 60%;
    display: block;
    border-radius: 1px;
    box-shadow: 0px 0px 10px grey;
    margin: auto;
  }

  .video{
    /*border: blue solid 1px;*/
    display: block;
    margin: 0 auto;
  }
}
@media screen and (max-width: 800px){
  .layout{
    width: 100%;
    display: block;
    border-radius: 1px;
    box-shadow: 0px 0px 10px grey;
    margin: auto;
  }
}

</style>



