<template>
<div class="root">
  <Header title="员工关怀" english_title="Employee care"/>
  <div class="container">
    <swiper class="swiper_container" :options="swiperOptions">
      <swiper-slide class="slide_item">
        <div class="item">
          <div class="item_image remuneration"></div>
          <div class="item_text">
            <div class="title">全面薪酬：</div>
            <div class="content">" 基本薪资、年底双薪、项目奖金、年度调薪。 "</div>
          </div>
        </div>
      </swiper-slide>
      <swiper-slide class="slide_item">
        <div class="item">
          <div class="item_image welfare">
          </div>
          <div class="item_text">
            <div class="title">生活福利:</div>
            <div class="content">" 六险一金、年度体检、就近租房补贴、包午餐。"</div>
          </div>
        </div>
      </swiper-slide>
      <swiper-slide class="slide_item">
        <div class="item">
          <div class="item_image entertainment"></div>
          <div class="item_text">
            <div class="title">休闲娱乐：</div>
            <div class="content">" 兴趣俱乐部、节日礼物、团建基金、年度旅游。 "</div>
          </div>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
      <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
  </div>
</div>
</template>

<script>
import Header from "../../components/Header";
export default {
  name: "EmployeeCare",
  components:{
    Header
  },
  data(){
    return{
      swiperOptions: {
        slidesPerView: 2,
        spaceBetween: 10,
        slidesPerGroup: 1,
        loop: true,
        loopFillGroupWithBlank: false,
        //
        // pagination: {
        //   el: '.swiper-pagination',
        //   clickable: true
        // },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  }
}
</script>

<style scoped>
@media screen and (min-width: 1000px){
  .container{
    width: 70%;
    margin: 0 auto;
    padding: 0;
    /*border: red solid 1px;*/
  }


  .slide_item{
    /*border: blue solid 1px;*/
    background-color: #FFFFFF;
    margin: 0 auto;
    height: 45vh;
    width: auto;
    display: block;
    padding-top: 1vh;
    padding-bottom: 1vh;
  }

  .item{
    margin: 0 auto;
    width: 80%;
    height: 100%;
    /*border: grey solid 1px;*/
    border-radius: 1vh;
    background-color: grey;
    /*
      box-shadow: h-shadow v-shadow blur spread color inset;
    */
    box-shadow: 0px 0px 1vh grey;
  }

  .item_image{
    /*border: red solid 1px;*/
    /*background-color: #0d63b8;*/
    height: 60%;
    border-top-left-radius: 1vh;
    border-top-right-radius: 1vh;
  }

  .remuneration{
    background-image: url("https://h5.store.bolegames.com/bole_campus/static/EmployeeCare/remuneration.png");
    background-position: center;
    background-size: cover;
  }

  .welfare{
    background-image: url("https://h5.store.bolegames.com/bole_campus/static/EmployeeCare/welfare.jpeg");
    background-position: center;
    background-size: cover;
  }

  .entertainment{
    background-image: url("https://h5.store.bolegames.com/bole_campus/static/EmployeeCare/entertainment.jpg");
    background-position: center;
    background-size: cover;
  }

  .item_text{
    background-color: #FFFFFF;
    height: 40%;
    border-bottom-left-radius: 1vh;
    border-bottom-right-radius: 1vh;
  }

  .title{
    /*border: red solid 1px;*/
    padding-left: 2vh;
    padding-top: 1.5vh;
    font-size: 3vh;
    font-family: "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  }
  .content{
    padding: 1.5vh;
    color: grey;
    font-size: 1.9vh;
    font-family: "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  }

  .swiper_container{
    margin:  auto;
    padding: 10px;
  }

  .swiper-button-prev{
    --swiper-theme-color: #ffffff;
    --swiper-navigation-size: 2vh;
    width: 5vh;
    height: 5vh;
    background: rgb(49, 152,224);
    -moz-border-radius: 2.5vh;
    -webkit-border-radius: 2.5vh;
    border-radius: 2.5vh;
  }
  .swiper-button-next {
    --swiper-theme-color: #ffffff;
    --swiper-navigation-size: 2vh;
    width: 5vh;
    height: 5vh;
    background: rgb(49, 152,224);
    -moz-border-radius: 2.5vh;
    -webkit-border-radius: 2.5vh;
    border-radius: 2.5vh;
  }

}

@media screen and (max-width: 1000px){
  .root{
    display: none;
  }
}
</style>



