<template>
  <div class="root">
    <div class="container">
      <div class="short_line">
      </div>
      <div class="main_title">
        <p class="main_title_text">{{title}}</p>
      </div>
      <div class="sub_title">
        <p class="sub_title_text">{{english_title}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  props:{
    title:            String,
    english_title:    String,
  }
}
</script>

<style scoped>


.container{
  /*border: blue solid 2px;*/
  text-align: center;
}
div{
  display: block;
}

.short_line{
  /*border: yellow solid 2px;*/
  margin: 0;
  width: 4vw;
  border: solid blue;
  border-radius: 0.4vw;
  display: inline-block;
  background-color: blue;
  /*border-collapse: separate;*/
}
.main_title{
  /*border: blue solid 2px;*/
  padding: 0;
  margin: 0;
}
@media screen and (min-width: 800px) {
  .root{
    /*border: purple solid 2px;*/
    padding-top: 8vh;
    padding-bottom: 6vh;
    margin-top: 8vh;
  }
  .main_title_text {
    font-family: "微软雅黑", "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    font-size: 5vh;
    font-weight: 400;
    margin: 0;
  }
}
@media screen and (max-width: 800px){
  .root{
    /*border: purple solid 2px;*/
    padding-top: 20px;
    padding-bottom: 30px;
  }
  .main_title_text {
    font-family: "微软雅黑", "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    font-size: 42px;
    font-weight: 400;
    margin: 0;
  }

}

.sub_title{
  /*border: blue solid 2px;*/
  margin: 0;
  padding: 0;
}
.sub_title_text{
  font-size: 2.5vh;
  font-family: "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #808080;
  margin: 0;
}
</style>



