<template>
<div class="root">
  <div class="video-container">
    <video class="video" preload="auto" loop muted autoplay>
      <source src="https://h5.store.bolegames.com/bole_campus/assets/InviteTheme/official.mp4"/>
      </video>
  </div>
  <div class="text-content">
    <h3 class="theme-text">博乐科技2022届校园招聘</h3>
    <h1 class="theme-slogan">让世界 更快乐</h1>
    <div class="deliver_container">
      <button class="deliver_button"><a href="https://www.nowcoder.com/careers/bolegame/68408?ncsr=fwZeOW" target="_blank" style="text-decoration: none;display: block;color: #0d63b8">立即投递</a></button>
    </div>
    <div class="next_page_container">
      <button class="next_page_button" @click="jump2NextComponent"><i class="u-arrow u-arrow-down "></i></button>
    </div>
  </div>

</div>
</template>

<script>
import Header from "../../components/Header";
export default {
  name: "InviteTheme",
  methods:{
    jump2Deliver(){
      alert('跳转到投递界面')
    },
    jump2NextComponent(){
      document.querySelector(`#SchoolRecruitmentPositionsID`).scrollIntoView({
        behavior: "smooth",   // 平滑过渡
        block:    "start"     // 上边框与视窗顶部平齐。默认值
      });
    }
  },
  components:{
    Header
  }
}
</script>

<style scoped>
@media screen and (max-width: 800px){
  .root{
    display: none;
  }
}
.root {
  position: relative;
  /*height: 100%;*/
}
  .video-container{
    position: relative;
    margin: 0 auto;
    padding: 0;
  }

  .video{
    /*border: blue solid 1px;*/
    /*position: absolute;*/
    display: inline-block;
    width: 100%;
    object-fit: cover;
    height: 92vh;
    /*min-height: 661px;*/
  }

  .text-content{
    position: absolute;
    top: 15vh;
    left: 0;
    width: 100%;
    min-width: 661px;
    height: 100%;
    color: #FFFFFF;
    z-index: 2;
    text-align: center;
  }

  .theme-text{
    /*border: red solid 2px;*/

    /*margin-top: 165px;*/
    /*margin-bottom: 10px;*/
    font-size: 3vh;
    font-weight: 300;
    text-align: center;

  }
  .theme-slogan{
    /*border: red solid 2px;*/

    margin-top: 1vh;
    font-size: 10vh;
    font-weight: 700;
    text-align: center;
  }
  .deliver_container{
    /*border: red solid 2px;*/
    text-align: center;
  }

  .deliver_button{

    /*position: absolute;*/
    margin-top: 6vh;

    background-color: #FFFFFF;
    color: #0d63b8!important;
    padding: 1.8vh 1.8vw;
    text-align: center;
    display: inline-block;
    font-size: 2vh;
    text-decoration: none;
    width: 12vw;
    height: 8vh;
    border-radius: 4vh;
    border: #FFFFFF solid 0.4vh;
  }

  .next_page_container{
    margin-top: 10vh;
    text-align: center;
  }
  .next_page_button{
    background: none;
    border: #FFFFFF solid 0.5vh;
    width: 10vh;
    height: 10vh;
    border-radius: 10vh;
  }
  .u-arrow {
    display: block;
    width: 3vh;
    height: 3vh;
    border-top: 0.5vh solid #FFF;
    border-right: 0.5vh solid #FFF;
    margin: auto;
  }

  .u-arrow-down {
    transform: rotate(135deg);
  }
</style>



